              <p class=" c5"><span class=" c10">Whenever we transfer your personal information out of the EEA to the U.S. or countries not deemed by the European Commission to provide an adequate level of personal information protection, the transfer will be based on a data transfer mechanism recognized by the European Commission as providing adequate protection for personal information.</span>
              </p>
              <p class=" c5"><span class=" c10">Please contact us if you want further information on the specific mechanism used by us when transferring your personal information out of the EEA.</span>
              </p>
              <h2 id="h.rfokgwopblg1" class=" c5"><span
                class=" c15">COOKIES POLICY</span></h2>
              <p class=" c5"><span class=" c10">We understand that your privacy is important to you and are committed to being transparent about the technologies we use. In the spirit of transparency, this policy provides detailed information about how and when we use cookies on our Site.</span>
              </p>
              <p class=" c7"><span class=" c6">Do we use Cookies?</span></p>
              <p class=" c5"><span class=" c10">Yes. We and our marketing partners, affiliates, and analytics or service providers use cookies, web beacons, or pixels and other technologies to ensure everyone who uses the Site has the best possible experience.</span>
              </p>
              <p class=" c7"><span class=" c6">What is a Cookie?</span></p>
              <p class=" c5"><span class=" c10">A cookie (“Cookie”) is a small text file that is placed on your hard drive by a web page server. Cookies contain information that can later be read by a web server in the domain that issued the cookie to you. Some of the cookies will only be used if you use certain features or select certain preferences, and some cookies will always be used. You can find out more about each cookie by viewing our current cookie list below. We update this list periodically, so there may be additional cookies that are not yet listed. Web beacons, tags and scripts may be used in the Site or in emails to help us to deliver cookies, count visits, understand usage and campaign effectiveness and determine whether an email has been opened and acted upon. We may receive reports based on the use of these technologies by our service/analytics providers on an individual and aggregated basis.</span>
              </p>
              <p class=" c7"><span class=" c6">Why do we use Cookies?</span></p>
              <p class=" c5"><span class=" c10">We generally use Cookies for the following purposes:</span>
              </p>
              <ul class=" c23 lst-kix_bz99p0mrg37g-0 start is-list has-bullets">
                <li class=" c0"><span class=" c17">To recognize new or past customers.</span>
                </li>
                <li class=" c0"><span class=" c17">To store your password if you are registered on our Site.</span>
                </li>
                <li class=" c0"><span class=" c17">To improve our Site and to better understand your visits on our platforms and Site.</span>
                </li>
                <li class=" c0"><span class=" c17">To integrate with third party social media websites.</span>
                </li>
                <li class=" c0"><span class=" c17">To serve you with interest-based or targeted advertising.</span>
                </li>
                <li class=" c0"><span class=" c17">To observe your behaviors and browsing activities over time across multiple websites or other platforms.</span>
                </li>
                <li class=" c0"><span class=" c17">To better understand the interests of our customers and our website visitors.</span>
                </li>
              </ul>
              <p class=" c5"><span class=" c10">Some Cookies are necessary for certain uses of the Site, and without such Cookies, we would not be able to provide many services that you need to properly use the Site. These Cookies, for example, allow us to operate our Site so you may access it as you have requested and let us recognize that you have created an account and have logged into that account to access Site content. They also include Cookies that enable us to remember your previous actions within the same browsing session and secure our Sites.</span>
              </p>
              <p class=" c5"><span class=" c10">We also use functional Cookies and Cookies from third parties for analysis and marketing purposes. Functional Cookies enable certain parts of the site to work properly and your user preferences to remain known. Analysis Cookies, among other things, collect information on how visitors use our Site, the content and products that users view most frequently, and the effectiveness of our third party advertising. Advertising Cookies assist in delivering ads to relevant audiences and having our ads appear at the top of search results. Cookies are either “session” Cookies which are deleted when you end your browser session, or “persistent,” which remain until their deletion by you (discussed below) or the party who served the cookie. Full details on all of the Cookies used on the Site are available at our Cookie Disclosure table below.</span>
              </p>
              <p class=" c7"><span class=" c6">How to disable Cookies.</span></p>
              <p class=" c5"><span class=" c10">You can generally activate or later deactivate the use of cookies through a functionality built into your web browser. To learn more about how to control cookie settings through your browser:</span>
              </p>
              <p class=" c5"><span class=" c11">Click </span><span
                class=" c3"><a
                href="https://support.mozilla.org/en-US/kb/enable-and-disable-cookies-website-preferences"
                class=" c20">here</a></span><span class=" c10"> to learn more about the “Private Browsing” setting and managing cookie settings in Firefox;</span>
              </p>
              <p class=" c5"><span class=" c11">Click </span><span
                class=" c3"><a
                href="https://support.google.com/chrome/answer/95647?hl%3Den" class=" c20">here</a></span><span
                class=" c10"> to learn more about “Incognito” and managing cookie settings in Chrome;</span>
              </p>
              <p class=" c5"><span class=" c11">Click </span><span
                class=" c3"><a
                href="https://support.microsoft.com/en-us/help/17442/windows-internet-explorer-delete-manage-cookies"
                class=" c20">here</a></span><span class=" c10"> to learn more about “InPrivate” and managing cookie settings in Internet Explorer; or</span>
              </p>
              <p class=" c5"><span class=" c11">Click </span><span
                class=" c3"><a
                href="https://support.apple.com/guide/safari/manage-cookies-and-website-data-sfri11471/mac"
                class=" c20">here</a></span><span class=" c10"> to learn more about “Private Browsing” and managing cookie settings in Safari.</span>
              </p>
              <p class=" c5"><span class=" c11">If you want to learn more about cookies, or how to control, disable or delete them, please visit
                <!-- --> </span><span class=" c3"><a href="http://www.aboutcookies.org"
                                                                  class=" c20">http://www.aboutcookies.org</a></span><span
                class=" c10"> for detailed guidance. In addition, certain third party advertising networks, including Google, permit users to opt out of or customize preferences associated with your internet browsing. To learn more about this feature from Google, click here.</span>
              </p>
              <p class=" c5"><span class=" c11">To control flash cookies, which we may use on our Site from time to time, you can go to this
                <!-- --> </span><span class=" c3"><a
                href="http://www.macromedia.com/support/documentation/en/flashplayer/help/settings_manager07.html"
                class=" c20">link</a></span><span class=" c10"> because Flash cookies cannot be controlled through your browser settings. Please note that if you decline the use of Cookies, some functions of the website may be unavailable and we will not be able to present personally tailored content and advertisements to you.</span>
              </p>
              <p class=" c5"><span class=" c10">We may link the information collected by Cookies with other information we collect from you pursuant to this Privacy Policy and use the combined information as set forth herein. Similarly, the third parties who serve cookies on our Site may link your name or email address to other information they collect, which may include past purchases made offline or online, or your online usage information. If you are located in the European Economic Area, you have certain rights that are described above under the header “Notice to EU Data Subjects”, including the right to inspect and correct or delete the data that we have about you.</span>
              </p>
